.pagination{
    display: flex;
    width: 140px;
    justify-content: flex-end;
    color: #fff;
    font-weight: bold;
    font-size: 22px;
    padding: 10px;
    position: absolute;
    bottom: -70px;
    left: 250px;
}
.pagination div {
    margin: 0 6px;
    cursor: pointer;
    color: #7295af;
}
.d-none{
    display: none;
}
