.container{
    width: 700px;
    height: 850px;
    background-color: #fff;
    margin: 20px auto;
    border-radius: 8px;
    position: relative;
}

.search-input{
  width: 100%;
  height: 70px;
  position: relative;

  padding: 0;
  text-indent: 40px;

  background: url(data:image/png;base64,) no-repeat;
  background-size: 20px;
  background-position: 15px 27px;
  background-color: #e9eef3;
  color: #1d3648;
  font-size: 16px;
  font-weight: bold;

  border: none;
  outline: none;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.filter{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #1d3648;
    width: 100%;
    border-bottom: 1px solid #eef0f3;
    height: 50px;
}
.filter-container{
    margin: 0 10px 0 70px;
}
.arrow_btn{
    border-radius: 50%;
    outline: none;
    margin: 3px;
}

.author-list{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 72px;
    width: 100%;
}
.grayBckg{
    background-color: #f4f6f9;
    border-top: 1px solid #eef0f3;
    border-bottom: 1px solid #eef0f3;

}
.count{
    font-size: 12px;
    margin-left: 10px;
    color: #b1b2b4;
}
.logo-name{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: #67c9de;
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    margin: 0 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.full-name{
    width: 250px;
    color: #1d3648;
    font-size: 16px;
    margin-left: 10px;
    font-weight: bold;
    line-height: 24px;
}
.count_pub{
    color: #b1b2b4;
    font-size: 16px;
    font-weight: normal;
}

.medal{
    width: 260px;
    height: 100%;
}
.pageviews{
    width: 100px;
    color: #1d3648;
    font-size: 16px;
    font-weight: bold;
}
.color-67c9de{
    background-color: #67c9de;
}
.color-ba6fcb{
    background-color: #ba6fcb;
}
.color-e39473{
    background-color: #e39473;
}
.color-5aa9e6{
    background-color: #5aa9e6;
}
.color-ac5061{
    background-color: #ac5061;
}
.color-9ec07f{
    background-color: #9ec07f;
}
.color-b4507b{
    background-color: #b4507b;
}
.color-345feb{
    background-color: #345feb;
}

.pagination{
    display: -webkit-flex;
    display: flex;
    width: 140px;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    color: #fff;
    font-weight: bold;
    font-size: 22px;
    padding: 10px;
    position: absolute;
    bottom: -70px;
    left: 250px;
}
.pagination div {
    margin: 0 6px;
    cursor: pointer;
    color: #7295af;
}
.d-none{
    display: none;
}

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(#0c1d2b, #285f88);
  background-image: linear-gradient(#0c1d2b, #285f88);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

